    .chat-app, #root {
        height: 100%;
    
        .form-window, .profile-window {
            padding-top: 49px;
            width: 100%;
            height: 100vh;
            display: grid;
            place-items: center;
        
            form, .profile-box {
            text-align: center;
            width: 420px;

            .form-header, .profile-header {
                width: 100%;
                margin: 20px 0;
                font-family: $title-font;
                font-size: 62px;
                color: $accent-color;
                text-shadow: 1px 1px $main-color;
                border: 2px solid $accent-color;
                line-height: 1.3;
                letter-spacing: 6px;
                text-transform: uppercase;
                text-shadow: 1px 1px 1px darken($main-color, 15%);
            }

            .form-header.profile, .profile-header.profile {
                font-size: 32px; 
                margin-bottom: 10px; 
            }

            .form-subheader {
                background-image: linear-gradient(to bottom right, $accent-color, darken($accent-color, 25%));
                font-size: 30px;
                font-family: $title-font;
                color: darken($main-color, 5%);
                border: 1px solid darken($main-color, 15%);
                line-height: 1.1;
                letter-spacing: 5px;
                text-transform: uppercase;
            }
            .form-errors{
                border: 2px solid $secondary-color;
                margin-top: 20px;
                padding: 10px 20px;

                h4 {
                    display: inline-block;
                    width: 100%;
                    padding-bottom: 10px;
                    color: $accent-color;
                }

                h4:last-of-type {
                    padding-bottom: 0;
                }
            }

            .form-body, .profile-body {
                width: 100%;
                padding: 10px 0;
                border: 1px solid darken($main-color, 15%);
                background: rgb(224,223,216);
                background-image: linear-gradient(to bottom right, darken($accent-color, 25%), lighten($main-color, 10%));
                box-shadow: inset 5px 4px 25px -17px $accent-color; 
                
                .profile-section{
                    padding: 10px 0;
                    border-bottom: 1px solid $main-color;
                    h4 {
                        color: $accent-color;
                        margin-bottom: 8px;
                        font-size: 14px;
                    }
                    input, textarea {
                        font-size: 14px;
                        color: $accent-color;
                    }
                }
                profile-section .btn {
                    margin-top: 10px;
                }
            }

            .form-body.login {
                padding: 20px 0;
            }
        
        }
    }
}

@media (max-width: 680px){
   .chat-app, #root {

        .form-window, .profile-window {
        
            form, .profile-box {
                width: 300px;

                .form-header, .profile-header {
                    font-size: 40px;
                }

                .form-subheader {
                    font-size: 20px;
                    letter-spacing: 4px;
                }

            }
        }
    }
}